.Resume{
	display: inline-block;
	width: 100%;
	color: #373a47;
}

a{
	color: red;
}
a:hover,a:focus{
	text-decoration: none;
	color: #373a47;
}
@media all and (max-width: 600px) {
	iframe{
		display:none;
	}
	.center{
	width:0%;
	height: 0%;
	}
	.mobile{
		display:block;
		padding-top:200px;
		width: 100%;
		height:100%;
		font: bold 30px sans-serif;
	}
	.immobile{
		display:none;
	}
	.btn-default{
		background-image: none;
		margin:5px;
		width:120px;
		height:50px;
		border-radius: 0px;
	}
}
@media all and (min-width: 600px) { 
	iframe{
	margin: 0;
	padding: 0;
	border: none;
	width: 800px;
	height: 740px;
	overflow-y: hidden;
	}
	.mobile{
		display:none;
	}
	.immobile{
		font: bold 16px sans-serif;
		padding:10px;
	}
}


